export default {
	data() {
		return {
			username: '15652653114',
			password: '123456'

		}
	},
	onLoad() {

	},
	methods: {
		GOhonme() {
			this.$API.LOGIN.LOGIN(JSON.stringify({
				username: this.username,
				password: this.password
			})).then(res => {
				console.log(res)
			}).catch(err => {
				console.log(err)
				if (err.code == "0") {
					uni.setStorage({
						key: 'storage_key',
						data: JSON.stringify(err.data),
						success: function() {
							console.log('success');
						}
					});
					var $cookie = this.$cookie;
					//赋值
					this.$cookie.set('token', err.data.token)
					uni.navigateTo({
						url: '../bannerX/bannerX'
					});
				}
				if (err.code == "1") {
					uni.showToast({
						title: '账号密码错误',
						duration: 5000,
						icon: 'none'
					});
				}

			})
		},
		wxsq() {
			let _this = this
			uni.request({
				url: "/api/Home/Public/weixredirect_uri",
				method: "POST",
				success(res) {
					console.log(res.data.data)
					let {
						appID,
						appsecret
					} = res.data.data
					window.location.replace(
						"https://open.weixin.qq.com/connect/oauth2/authorize?" +
						"appid=" + appID + "&" +
						"redirect_uri=" + encodeURIComponent('http://saas.yjshangmao.com/Home/Public/redirect_uri') + "&" +
						"scope=snsapi_base#wechat_redirect")
				}
			})
		},
		goRetrieve() {
			uni.navigateTo({
				url: "../register/register"
			})
		},
		noPassword() {
			uni.navigateTo({
				url: "../retrieve/retrieve"
			})
		}

	}
}
